.card {
  justify-self: center;
  background-color: transparent;
  width: 200px;
  height: 330px;
  perspective: 1000px;
  &:hover &__inner {
    transform: rotateY(180deg);
  }
  &__text {
    color: $white;
    padding: 0.3em;
    position: absolute;
    bottom: 15px;
    white-space: pre;
    left: 0px;
    right: 0px;
    text-align: center;
    z-index: 1;
    text-transform: uppercase;
    font-weight: $font-weight-b;
    font-size: $font-size-xs;
  }
  &__inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
    &__front,
    &__back {
      position: absolute;
      width: 100%;
      height: 100%;
      -webkit-backface-visibility: hidden; /* Safari */
      backface-visibility: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 5px;
    }
    &__front {
      color: black;
      background-image: url(/images/method-draw-image.svg);
      background-size: 10%;
      img {
        max-width: 200px;
        z-index: 1;
      }
    }
    &__back {
      background-color: dodgerblue;
      color: white;
      transform: rotateY(180deg);
      display: grid;
      grid-template-rows: 30% 60% 10%;
      &__image {
        height: 100%;
        img {
          max-width: 100%;
          max-height: 100%;
        }
      }
      &__description {
        height: 100%;
        padding: 0 5px;
        font-weight: $font-weight-n;
        font-size: $font-size-xs;
        &__link {
          display: block;
          margin-top: 10px;
          a {
            font-weight: $font-weight-b;
            color: $white;
          }
        }
        &__code {
          display: block;
          svg {
            width: 20px !important;
            height: 20px;
            margin-top: 10px;
            padding-left: 5px;
          }
        }
      }
    }
  }
}
.button {
  background-color: $yellow;
  padding: 5px 0;
  margin: 5px;
  border: none;
  border-radius: 5px;
  min-width: 200px;
  font-size: $font-size-s;
  font-weight: $font-weight-b;
  color: $white;
}
